// stylelint-disable declaration-no-important, selector-list-comma-newline-after

//
// Headings
//

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  margin-bottom: $headings-margin-bottom;
  font-family: $headings-font-family;
  font-weight: $headings-font-weight;
  line-height: $headings-line-height;
  color: $headings-color; }

h1, .h1 {
  font-size: $h1-font-size; }

h2, .h2 {
  font-size: $h2-font-size; }

h3, .h3 {
  font-size: $h3-font-size; }

h4, .h4 {
  font-size: $h4-font-size; }

h5, .h5 {
  font-size: $h5-font-size; }

h6, .h6 {
  font-size: $h6-font-size; }

.lead {
  font-size: $lead-font-size - 0.25;
  font-weight: $lead-font-weight;
  margin-bottom: $lead-font-size;
  @include media-breakpoint-up(md) {
    font-size: $lead-font-size; } }

// Type display classes
.display-1 {
  font-size: $display4-size - 0.75;
  font-weight: $display1-weight;
  line-height: $display-line-height;
  @include media-breakpoint-up(sm) {
    font-size: $display3-size; }
  @include media-breakpoint-up(md) {
    font-size: $display2-size; }
  @include media-breakpoint-up(lg) {
    font-size: $display2-size - 1; }
  @include media-breakpoint-up(xl) {
    font-size: $display1-size; } }

.display-2 {
  font-size: $display2-size;
  font-weight: $display2-weight;
  line-height: $display-line-height; }

.display-3 {
  font-size: $display3-size;
  font-weight: $display3-weight;
  line-height: $display-line-height; }

.display-4 {
  font-size: $h1-font-size + 0.5;
  font-weight: $display4-weight;
  line-height: $display-line-height;
  @include media-breakpoint-up(md) {
    font-size: $display4-size; } }


//
// Horizontal rules
//

hr {
  margin-top: $hr-margin-y;
  margin-bottom: $hr-margin-y;
  border: 0;
  border-top: $hr-border-width solid $hr-border-color; }

//
// Emphasis
//

small,
.small {
  font-size: $small-font-size;
  font-weight: $font-weight-normal; }

mark,
.mark {
  padding: $mark-padding;
  background-color: $mark-bg; }

//
// Lists
//

.list-unstyled {
  @include list-unstyled; }

// Inline turns list items into inline-block
.list-inline {
  @include list-unstyled; }

.list-inline-item {
  display: inline-block;

  &:not(:last-child) {
    margin-right: $list-inline-padding; } }

//
// Misc
//

// Builds on `abbr`
.initialism {
  font-size: 90%;
  text-transform: uppercase; }

// Blockquotes
.blockquote {
  margin-bottom: $spacer;
  font: {
    size: $blockquote-font-size;
    weight: $font-weight-extra; } }


.blockquote-footer {
  display: block;
  font-size: 1rem;

  // back to default font-size
  color: $blockquote-small-color;

  &::before {
    content: "\2014 \00A0";

 } }    // em dash, nbsp
